@import url("https://fonts.googleapis.com/css?family=Josefin+Slab:100,100i,300,300i,400,400i,600,600i,700,700i|Montserrat:300,300i,400,400i,700,700i");

@font-face {
  font-family: "My Custom Font";
  src: url("../includes/fonts/Tryst-Regular.otf") format("opentype");
}

$accent: #fdcab8;
$primary: rgb(103, 103, 103);
$grey: #bdbdbd;

$heading-font: "Josefin Slab", serif;
$main-font: "Montserrat", sans-serif;

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-family: $main-font;
  color: $primary;
}

body {
  background: lighten($grey, 20%);
}
